@import url(https://fonts.googleapis.com/css?family=PT+Mono:regular);
.field {
  padding-top: 1rem;
  display: grid;
  grid-gap: 0.4rem;
  grid-template-columns: 8rem 8rem 8rem;
  grid-template-rows: 8rem 8rem 8rem;
  background-color: inherit;
  justify-content: center;
}

.sell {
  padding: 0.01px;
  width: 8rem;
  height: 8rem;
  background-color: rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: -webkit-grab;
  cursor: grab;
  font-size: 6rem;
  color: rgb(187, 42, 42);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'PT Mono';
}

html {
  font-size: 10px;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #8bccd8;
  min-height: 100vh;
}

h1 {
  color: rgb(231, 176, 114);
  font-size: 4rem;
}
.score {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-size: 1.5rem;
  margin: 1rem 0 2rem;
}
.score input {
  border: none;
  font-size: 2rem;
  padding: 1rem;
  text-align: center;
  background-color: inherit;
  display: block;
}
label {
  display: block;
  color: rgb(132, 107, 245);
  font-size: 1.2rem;
  padding: 1rem 0 0.3rem;
}

.draw {
  font-weight: 900;
  font-size: 1.5rem;
  color: rgb(255, 60, 0);
}

.player-active {
  border: 1px solid rgb(56, 28, 28);
  border-radius: 4px;
}

.hidden {
  opacity: 0;
}

button {
  background-image: linear-gradient(to right, #1d2b64 0%, #f8cdda 51%, #1d2b64 100%);
  margin: 0.5rem;
  padding: 0.5rem 2rem;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: rgb(255, 255, 255);
  box-shadow: 0 0 20px rgb(206, 193, 193);
  border-radius: 10px;
  outline: none;
  font-size: 1rem;
}

button:hover {
  background-position: right center; /* change the direction of the change here */
  color: rgb(255, 255, 255);
  text-decoration: none;
  cursor: pointer;
}

.button-active {
  transform: scale(1.3);
  background-image: linear-gradient(to right, #641d58 10%, #f782a5 81%, #dd6b71 100%);
}

.autoplay {
  display: inline;
}

.footer {
  height: 5rem;
  margin-top: 5rem;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  width: 100%;
}

.footer-img {
  width: 5rem;
}

.year {
    font-size: 2rem;
    color: rgb(36, 82, 70);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.settings {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.supplmented {
  width: 30%;
  height: 35%;
  background: #eee;
  border-radius: 5px;
}

.close {
  cursor: pointer;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 1rem;
  font-size: 2rem;
  transform: rotate(45deg);
}
.close:hover {
  transform: rotate(45deg) scale(1.3);
}

.zoom {
display: block;
margin: 0.5rem auto 0;
}

.icubovich{
  position: fixed;
  width: 20rem;
  height: 35rem;
  bottom: -10%;
  left: 100%;
  animation: icubovich 3s linear;
}

@keyframes icubovich {
  0%{
    left: 100%;
        transform: scale(1);

  }
  50%{
    left: 80%;
    transform: scale(1.5);
  }
  100%{
    left: 110rem;
        transform: scale(1);

  }
}


